.head {
    display: flex;
    height: 100vh;
}

.form {
    flex: 40% 1;
    /* margin: 30vh 20vh 50vh; */
    margin: 20vh 10vw 10vh 15vw;
    padding: 30px;
    height: -webkit-min-content;
    height: min-content;
    background: #FFFFFF;
    box-shadow: 0px 4px 84px rgba(0, 0, 0, 0.14);
    border-radius: 9px;
    /* font-size: 1.3em; */
}

.form h2 {
    margin: 10px 0px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content; 
    font-weight: 500;
    margin-left: 0px;
    color: #313C61;
}



.form input {
    padding: 8px;
    margin: 20px 0px;
    width: 95.5%;
    height: 40px;
    background: #F4F4F4;
    border-radius: 8px;
    font-size: 1em;
}

.form #submit {
    width: 100%;
    margin: auto;
    color: white;
    font-weight: 700;
    font-size: medium;
    transition: ease-in .2s;
    background: #575CE5;
    border-radius: 8px;
}

.form #submit:hover {
    background: #1419a1;
    ;
}

.flex-hr {
    display: flex;
    margin-top: 10px;
    align-items: center;
}

.flex-hr p {
    width: 10%;
    text-align: center;
    color: rgba(0, 0, 0, 0.5);
}

.g-btn-div {
    margin-top: 10px;
}

.g-logo {
    background: url(data:image/png;base64,);
    width: 30px;
    margin: 0px 5px;
    height: 27px;
}

.g-btn {
    width: 100%;
    display: flex;
    margin: auto;
    padding: 10px 0px;
    transition: ease-in .2s;
    border: 1px solid #575CE5;
    border-radius: 8px;
    color: #575CE5;
    align-items: center;
    justify-content: center;
}

.form .g-btn:hover {
    color: #1419a1;
    ;
}

.signup-p {
    font-weight: 500;
    font-size: 18px;
    text-align: center;
    color: #313C61;
    margin-top: 15px;
}

.signup-link {
    color: #575CE5;
}

.pic-div {
    flex: 90% 1;
    background-color: #c4d4d3;
}

.pic {
    background: url(demo/static/media/loginpic.0d90c2a0.png);
    background-size: contain;
    background-repeat: no-repeat;
    height: 100vh;
    background-position: right;
}
/* Sign Up */

.signup-pic-div{
    flex: 90% 1;
    background-image: linear-gradient(6deg, #1419a1, #575ce5);
}
.signup-pic-div .box{
    padding: 16.276vw 11.719vw;
    color: white;
}

.signup-pic-div .box h1{
    color: white;
}
.signup-pic-div span{
    color: aqua;
}
.page {
    padding: 30px;
