    /* padding: 10px; */
}

.search input {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 500;
    font-size: 26px;
}

.search select {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 500;
    font-size: 26px;
    line-height: 34px;
    /* identical to box height */

    text-align: right;

    color: #8390BB;
}

.search button {
    background-color: white;
}


